}
.text-faded-green {
  color: rgba(80, 134, 67, 0.34);;
}
.text-faded-green:hover, .text-faded-green:focus  {
  color: rgba(80, 134, 67, 0.64);
  cursor: pointer;
}
.lead-small {
  font-size: 1.14rem;
  margin-bottom: .25rem;
  font-weight: 300;
}
.bql {
    position: relative;
    padding: 10px 15px;
    background-color: #f5f8fa;
    border-radius: 9px;
}
.bql .bqm:first-child:before {
    left: auto;
    right: -8px;
    margin-left: 0;
    margin-right: 2px;
    border-right: 0;
    border-left: 6px solid #3097D1;
}
.bql .bqm:first-child:after {
    position: absolute;
    top: 16px;
    left: -8px;
    content: '';
    display: inline-block;
    width: 0;
    height: 0;
    margin-left: 2px;
    vertical-align: middle;
    border-top: 6px solid transparent;
    border-bottom: 6px solid transparent;
    border-right: 6px solid #f5f8fa;
}
.axr {
    color: #636c72 !important;
}
